*{
	font-family: Heebo;
}



#WhatIsMain{
	width: calc(100% - 15vw);
	position: absolute;
	top:2vw;
	right:15vw;
	display: flex;
	direction: rtl;
	flex-direction: column;

}




.ContentDiv{
	width: 70vw;
	height: -webkit-fit-content;
	height: -moz-fit-content;
	height: fit-content;
	margin: 0 auto;
	text-align: right;
	margin-top: 0vw;
	direction: ltr;

}
.ContentText{
	font-weight: 1000;
	font-size: 1.5vw;
}







.sidenavbar{
	position: fixed;
	right: 0;
	width: 15vw;
	background-color: rgb(15,18,89);
	height: 100%;
	top:0;
	padding: 0;
	display: flex;
	flex-direction: column;
	align-content: stretch;
	justify-content: space-evenly;
	overflow-y: scroll;
}

a{
	text-decoration: none;

}

/* width */
.sidenavbar::-webkit-scrollbar {
  width: 0.5vw;

}

/* Track */
::-webkit-scrollbar-track {
   background: transparent;
}
 
/* Handle */
::-webkit-scrollbar-thumb {
   background: #244242;
  border-radius: 10vw;
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: rgb(40,40,40);
}

.sidenavtabs{
	display: block;
	height: 1.7vw;
	color:black;
	background-color: transparent;
	direction: rtl;
	margin-right:2vw;
/*	margin-bottom: 1.8vw;*/
}
.sidenavtabs a{
	color:white;
	font-size: 1.5vw;
	display: flex;
	justify-content: flex-start;
	height: 100%;
	align-items: center;
	padding-right: 1vw;
	padding-top: 0.5vw;
	padding-bottom: 0.5vw;
	transition: 0.2s;
}
.sidenavtabs a:hover{
	transform: scale(1.0);
	margin-right: 1vw;
	transition: 0.2s;
}

.activesidenavtabs{
	height: 4vw;
}
.activesidenavtabs a::before{
	content:'';
	width: 2vw;
	height: 2vw;
	background-color:rgb(15,18,89);
	border-bottom-left-radius: 1vw;
	box-shadow: -10px 10px 0px 0px white;
	position: absolute;
	left:0;
	top:-2vw;
}

.activesidenavtabs a::after{
	content:'';
	width: 2vw;
	height: 2vw;
	background-color:rgb(15,18,89);
	border-top-left-radius: 1vw;
	box-shadow: -10px -10px 0px 0px white;
	position: absolute;
	left:0;
	bottom:-2vw;
}



.activesidenavtabs a{
	color:black;
	border-radius: 0px 20px 20px 0px;
	background-color: white;
	padding-top: 0.5vw;
	padding-bottom: 0.5vw;
	position: relative;

}
.activesidenavtabs a:hover{
	background-color: white;
	transform: scale(1);
	margin-right: 0;
}
.MainButtonDiv{
	display: flex;
	direction: rtl;
	justify-content: space-between;
	margin-right:4vw;
	margin-top: 0vw;
	height: 100px;
	width: -webkit-fit-content;
	width: -moz-fit-content;
	width: fit-content;
}

.MainButton{
	font-size: 1.5vw;
	width: -webkit-fit-content;
	width: -moz-fit-content;
	width: fit-content;
	min-width: 8vw;
	border:none;
	color: #0F1259;
	background-color: #FDDCC0;
	height: -webkit-fit-content;
	height: -moz-fit-content;
	height: fit-content;
	padding: 0.5vw 1vw 0.5vw 1vw;
	transition: 0.4s;
	transform: skewX(20deg);
	margin-left: 0.3vw;
	cursor: pointer;
}
.MainButton:hover{
	background-color: #FFC593;
}

.rightcurved{
	border-right: 1vw solid transparent;
	border-top-right-radius: 6vw;
	border-bottom-right-radius: 17vw;
	padding-left: 1vw;
	padding-right: 0vw;
	width: -webkit-fit-content;
	width: -moz-fit-content;
	width: fit-content;
}
.leftcurved{
	border-right: 1vw solid transparent;
	border-top-left-radius: 17vw;
	border-bottom-left-radius: 6vw;
	padding-left: 1vw;
	padding-right: 0vw;
	width: -webkit-fit-content;
	width: -moz-fit-content;
	width: fit-content;
}

.MainButton p{
	transform: skewX(-20deg);
	padding: 0;
	margin: 0;
}

.MainButtonActive{
	background-color: #FAA159;
}
.MainButtonActive:hover{
	background-color: #FAA159;
}

.ContentDivFirstStatePage{
	margin: 0 auto;
	margin-top: 3vw;
	display: flex;
	flex-wrap: wrap;
	width: 60vw;
	height: 30vw;
	justify-content: space-evenly;
	direction: rtl;
}
.FirstStatePageButton{
	width: 15vw;
	margin: 1vw;
	font-size: 1.4vw;
	font-weight: 1000;
	height: 10vw;
	border: none;
	box-shadow: 0.3vw 0.3vw 0.5vw rgb(200,200,200);
	border-radius: 1vw;
	background-color: #FAA159;
	transition: 0.4s;
	color: #0F1259;
}
.FirstStatePageButton:hover{
	transform: scale(1.1);
	transition: 0.2s;
	cursor: pointer;
}


#BackButton{
	position:fixed;
	bottom: 2vw;
	right:40%;
	direction: rtl;
	display: flex;
}
#FrontButton{
	position:fixed;
	bottom: 2vw;
	left: 33%;
	direction: rtl;
	display: flex;
}


.FrontBackButtons{
	background-color: #FAA159;
	padding:0.3vw 0vw  0.4vw 0vw;
	color:#0F1259;
	font-size: 2vw;
	border-radius: 30vw;
	border:none;
	cursor: pointer;
	margin-left: 1.1px;
	width: 10vw;
	transition: 0.3s;
}

.FrontBackButtons:hover{
	background-color:#F6841F;
	transition: 0.2s;
}
